.spot-plan-summary {
  &__item {
    display: inline-block;
    position: relative;
    color: #fff;
    margin-left: 3px;
    border-radius: 50%;
    width: 24px;
    height: 24px;
    text-align: center;

    &.--positive {
      background-color: $positive;
    }
    &.--mixed {
      background-color: $mixed;
    }
    &.--negative {
      background-color: $negative;
    }
  }
}



