.optimum-vis {
  margin-bottom: 0px;
  position: relative;
  width: 100%;
  color: $black;

  &__scale {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    margin-top: -12px;

    .scale-marker {
      width: 2px;
      height: 12px;
      background-color: $black;
      position: absolute;
      top: 0;
      margin-left: -1px;

      p {
        color: $black;
        position: absolute;
        left: 50%;
        top: 0;
        text-align: center;
        width: 26px;
        margin-left: -13px;
        margin-top: -25px;
      }

      &.--full {
        left: 50%;
      }
      &.--pos {
        &.--left {
          left: 34.375%;
        }
        &.--right {
          left: 65.625%;
        }
      }
      &.--mixed {
        &.--left {
          left: 18.75%;
        }
        &.--right {
          left: 81.25%;
        }
      }
      &.--neg {
        &.--left {
          left: 6.25%;
        }
        &.--right {
          left: 93.75%
        }
      }
    }
  }

  &__meta {
    margin-bottom: 6px;
    text-transform: uppercase;
    color: $jumbo;
  }

  &__visual {
    width: 100%;
    display: block;
    margin: 0 auto;
    position: relative;
    height: 12px;
    padding: 50px 0 35px 0;
    overflow-x: hidden;
    overflow-y: visible;

    &.--mini {
      padding: 18px 0 6px 0;
    }

    .data-vis {
      width: 100%;

      &__bg,
      &__current,
      &__ROC {
        position: absolute;
        top: 50%;
      }

      &__bg {
        height: 16px;
        margin-top: -8px;

        &.--mini {
          height: 4px;
          margin-top: -4px;
        }

        &.--pos {
          background-color: $positive;
        }
        &.--mix {
          background-color: $mixed;
        }
        &.--neg {
          background-color: $iron;
        }

        &.--sectors {
          width: 106%;
          left: -3%;
          border: 4px dashed #F5F5F5;
          height: 0px;

          &.--top {
            border: 17px dashed #F5F5F5;
            margin-top: -39px;
          }
          &.--bottom {
            margin-top: 0px;
          }

          &.--mini {
            border: 2px dashed #F5F5F5;
          }
        }
      }

      &__current {
        position: absolute;
        text-align: center;
        color: $black;
        width: 60px;
        margin-left: -30px;
        margin-top: -48px;
        font-weight: 500;

        &::after {
          display: block;
          content: "";
          height: 14px;
          width: 14px;
          background-size: cover;
          margin-top: -3px;
          background-image: url('images/icons/navigation-down--black.svg');
          margin-left: 23px;
        }

        &.--mini {
          width: 4px;
          height: 4px;
          margin-left: -2px;
          margin-top: 0px;

          &::after {
            height: 8px;
            width: 8px;
            background-size: cover;
            margin-top: -12px;
            background-image: url('images/icons/navigation-down--black.svg');
            margin-left: -2px;
          }
        }
      }

      &__ROC {
        position: absolute;
        height: 0px;
        border: 1px dashed $base;
        margin-top: 10px;

        &.--mini {
          margin-top: 2px;
        }
      }

      &__ROC-value {
        position: absolute;
        text-align: center;
        color: $jumbo;
        width: 60px;
        margin-left: -30px;
        margin-top: 10px;
      }
    }
  }

  &__markings {
    color: $jumbo;
    position: absolute;
    width: 100%;
    top: 12px;

    .optimum-vis__min {
      position: absolute;
      left: 0;

      &.--zero {
        width: 30px;
        text-align: center;
        margin-left: -15px;
        &::after {
          content: '';
          height: 16px;
          width: 2px;
          background-color: $jumbo;
          position: absolute;
          top: 23px;
          left: 14px;
        }
      }
    }
    .optimum-vis__max {
      position: absolute;
      right: 0;
    }
  }

  &.--outside {
    .optimum-vis__current {
      display: none;
    }
  }
}



