.info-card {
  background: $white;
  border-radius: 4px;
  box-shadow: $shadow-subtle;
  margin-bottom: 12px;

  &__expanded-section {
    display: none;
  }

  &.--expanded {
    .--hide-expanded {
      display: none;
    }
    .info-card__expanded-section {
      display: block;
    }
  }

  .expand-button {
    position: absolute;
    bottom: 0;
    margin-left: 6px;
    margin-bottom: 12px;
  }

  &__top {
    height: 24px;
    background: $gainsboro;
    padding: 0px 12px;
  }

  &__top h3,
  &__top span {
    margin: 0;
    display: inline-block;
    text-transform: uppercase;
    line-height: 24px;
  }

  &__top h3 {
    font-size: rem-calc(14);
    color: $font-dark-primary;
    font-weight: $font-weight-black;
  }

  &__top span {
    font-size: rem-calc(10);
    color: $font-dark-secondary;
    font-weight: $font-weight-light;
    float: right;
    margin-left: 6px;
  }

  &__body {
    padding: 10px 6px;
    height: 90px;
    display: flex;
    justify-content: space-between;

    .datapoint {
      flex: 1 1 33%;
      border-left: 1px solid rgba(0, 0, 0, 0.1);
      height: 100%;
      padding: 0 6px;

      @media #{$medium-up} {
        padding: 0 8px;
      }

      &:first-child { border-left: none; }

      p {
        margin-bottom: 0px;
      }

      .title {
        position: relative;

        &::before {
          content: '';
          height: 10px;
          width: 10px;
          border-radius: 10px;
          display: inline-block;
          position: relative;
          margin-right: 2px;
        }

        &.--positive {
          &::before {
            background-color: #27AE60;
          }
        }
        &.--mixed {
          &::before {
            background-color: #f1c04b;
          }
        }
        &.--negative {
          &::before {
            background-color: #EB5757;
          }
        }
      }

      .main-text {
        color: $font-dark-primary;
        font-size: 0px;
        font-weight: bold;
        position: relative;

        // NOTE: IMPORTANT! THIS WILL STOP ANY TEXT NOT IN A SPAN FROM APPEARING.
        // It is a fix to hide any unintentional whitespace not inside the spans
        span {
          font-size: rem-calc(24);
        }

        span.value-unit {
          font-size: rem-calc(18);
          text-transform: lowercase;
        }
      }

      .sub-text {
        color: $font-dark-secondary;
        font-size: rem-calc(10);
        font-weight: normal;
        text-transform: uppercase;
        margin-bottom: 0px;

        &.title {
          margin-bottom: 0px;
        }
      }
    }
  }
}



