.footer {
  font-size: $small-font-size;
  border-top: 1px solid $gainsboro;

  &,
  a,
  p {
    color: $font-dark-secondary;
  }
}

.footer__links {
  list-style: none;
  margin: 0;

  li {
    display: inline-block;
    @include xy-gutters($gutter-position: left right);
  }
}



